.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  background-image: url('./images/discussion.png');
  background-repeat: no-repeat;
  background-size: cover;
}

.App-intro-title {
  background-image: url('./images/wave.png');
  background-repeat: no-repeat;
  background-size: 80%;
}

.wave {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  background-image: url('./images/wave.svg');
  background-repeat: no-repeat;
}

.keynotes {
  background-color: #f7f7f7;
}

.speakers {
  opacity: 0.93;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#1ea4c5), to(#0d6b93));
  background-image: -webkit-linear-gradient(top, #1ea4c5, #0d6b93);
  background-image: -o-linear-gradient(top, #1ea4c5, #0d6b93);
  background-image: linear-gradient(to bottom, #1ea4c5, #0d6b93);
}

.agenda {
  background-color: #f7f7f7;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



